@charset "UTF-8";

// Styles for the Identity Toolkit site - to be moved to a child theme of Redwood later.

.site-identity-guide {

  //
  // ------------------------------------
  // Sticky header styles only for desktop LG breakpoint and up
  // ------------------------------------
  //
  @include grid-media("lg") {
    .su-brand-bar,
    .masthead {
      position: fixed;
      width: 100%;
    }

    .su-brand-bar {
      top: 0;
    }

    .masthead {
      top: 30px; // this is the brand bar height
      background-color: $su-color-white;
    }

    h2,
    h3,
    h4,
    h5 {
      scroll-margin-top: 180px; // So anchor links don't get hidden behind fixed header
    }

    &.home {
      .bannertext {
        @include margin(170px null null); // masthead height
      }
    }


    &:not(.home) {
      main {
        @include margin(170px null null);
      }
    }

    &.logged-in {
      .su-brand-bar {
        top: 32px; // WP admin bar height
      }

      .masthead {
        top: 62px; // brand bar height + WP admin bar height
      }

      h2,
      h3,
      h4,
      h5 {
        scroll-margin-top: 212px;
      }
    }
  }

  // Sticky left sidebar only for XL breakpoint and up, and only for non-homepage
  @include grid-media("xl") {
    &:not(.home) {
      #sidebar--left {
        position: sticky;
        top: 180px;
        height: max-content;
        overflow-y: auto;
      }

      &.logged-in {
        #sidebar--left {
          top: 212px; // masthead + WP admin bar height
        }
      }
    }
  }

  // Show only current page's child items/siblings in left nav widget
  .widget_page_nav_widget {
    li:not(.current_page_item):not(.current_page_parent):not(.current_page_ancestor) {
      ul {
        display: none;
      }
    }
  }

  // Homepage fullwidth section styles
  .wp-block-group {
    &.fullwidth {
      @include margin(null -50vw);
      @include modular-spacing('padding', 5 null);
      @include modular-spacing('margin', 5 null);
      position: relative;
      left: 50%;
      right: 50%;
      width: 100vw;

      .wp-block-group__inner-container {
        @include margin(null auto);

        h2 {
          @include modular-typography(4);
          @include margin(null null 1em);
        }

        > * {
          @include margin(null auto);
          max-width: 100%;
        }

        .widget_info_box_widget {
          h2 {
            @include modular-typography(2);
          }
        }

        .wp-block-columns {
          h3,
          figure + h3 {
            @include margin(0 null null);
          }
        }
      }
    }
  }

  @include grid-media('md') {
    &.one-column:not(.left-nav) {
      .bannertext {
        .wp-block-columns {
          @include margin(null 0);
        }
      }
    }
  }

  .wp-block-group.fullwidth > div,
  .bannertext.fullwidth {
    @include rw-centered-column;
  }

  //
  // ------------------------------------
  // Gutenberg columns related styles
  // ------------------------------------
  //

  h2,
  h3 {
    + .wp-block-columns {
      @include margin(1.6em null null);
    }
  }

  .wp-block-column {
    > ul:first-child {
     @include margin(0 null null);
    }
  }

  .wp-block-columns {
    @include grid-media('sm') {
      &.has-2-columns {
        &.has-2-columns-1to2 {
          grid-template-areas: "col-1 col-2";
          grid-template-columns: 1fr 2fr;
          grid-column-gap: 36px;
        }

        // Have to repeat grid-column-gap here for auto-prefixer to do its work for IE
        &.has-2-columns-2to1 {
          grid-template-areas: "col-1 col-2";
          grid-template-columns: 2fr 1fr;
          grid-column-gap: 36px;
        }

        &.has-2-columns-1to3 {
          grid-template-areas: "col-1 col-2";
          grid-template-columns: 1fr 3fr;
          grid-column-gap: 36px;
        }

        // No gaps between columns for color swatches, e.g., "Accent Colors" and "Web Interactive Colors" pages
        &.colors {
          grid-template-areas: "col-1 col-2";
          grid-template-columns: 1fr 1fr;
          grid-column-gap: 0;
        }
      }

      &.has-3-columns {
        &.colors {
          grid-template-areas: "col-1 col-2 col-3";
          grid-template-columns: 1fr 1fr 1fr;
          grid-column-gap: 0;
        }
      }

      @include grid-media-between('sm','md') {
        &.has-4-columns {
          grid-template-areas: "col-1 col-2" "col-3 col-4";
          grid-template-columns: 1fr 1fr;
          grid-column-gap: 36px;
          grid-row-gap: 36px;
        }
      }
    }
  }

  .wp-block-code {
    background-color: $su-color-black;
    color: $su-color-white;
    height: 20rem;
    font-size: 0.9em;
  }

  .post__content {

    // Special class "demo" for heading with small font size and a light grey top border, e.g., "Wordmark" page.
    .demo {
      @include padding(0.6rem null);
      margin-top: 50px;
      font-size: 1em;
      font-weight: $su-font-regular;
      border-top: 2px solid $su-color-cool-grey-25;
    }

    h3,
    h4 {
      + .demo {
        @include margin(1.6em null null);
      }
    }

    p {
      &:first-child {
        + h2 {
          @include margin(1em null null);
        }
      }
    }

    // Big number ordered list, used in e.g., "Best Practices" under "Brand"
    .big-number {
      @include padding(null null null 0);
      @include margin(2em null null);
      padding-left: 0;
      counter-reset: list-counter;
      list-style-type: none;

      li {
        @include margin(null null 2em);
        counter-increment: list-counter;

        &::before {
          @include margin(-0.3em 0.4em null null);
          display: block;
          content: counter(list-counter);
          font-size: 2.4em;
          font-weight: $su-font-semi-bold;
          text-align: right;
          float: left;
          width: 1em;
          color: get-color(brand--bright);
        }
      }

      h3 {
        @include margin(null null 0.2em);
        display: flex;
        font-size: 1.4em;
      }

      p {
        @include margin(null null null 3.4em);
      }
    }

  }

  // Light grey box style for helpful tips, note, short piece of contact info etc
  .tip {
    @include modular-spacing("padding", 0);
    @include margin(1.4em null);
    background-color: $su-color-fog;
  }

  .tip,
  .well {
    .wp-block-group__inner-container {
      > *:last-child {
        @include margin(null null 0);
      }
    }
  }

  // Common style for adding FontAwesome icon as pseudo elements
  ul.download li a::after,
  .download a::after,
  .request a::after,
  .jump a::after,
  .do::before,
  .dont::before {
    font-family: "Font Awesome 5 Free";
    display: inline-block;
  }


  // Icons in front of "Do" and "Don't"
  .do,
  .dont {
    font-size: 1.8em;

    &::before {
      @include margin(null 0.3em null null);
    }
  }

  h4 {
    &.do,
    &.dont {
      @include modular-typography(1);
    }
  }

  .do {
    &::before {
      content: "\f00c";
      color: #94a04c;
    }
  }

  .dont {
    &::before {
      content: "\f00d";
      color: $su-color-bright-red;
    }
  }

  // Jump link buttons (chocolate colored buttons with down arrow), e.g., near top of the "Digital -> Web Design" page
  .jump {
    a {
      background-color: $su-color-chocolate;

      &::after {
        content: "\f063";
        font-size: 0.9em;
      }

      &:hover,
      &:focus {
        background-color: $su-color-bright-red;
      }
    }
  }

  // Download buttons (lagunita colored buttons with icon), e.g., on the "Visual Identity -> Wordmark" page
  // Also include links in ul's that are styled as download buttons
  .download {
    @include margin(0 null null);
    @include padding(null null null 0);
    list-style-type: none;

    @include grid-media-only("xs") {
      text-align: center;
    }

    li {
      display: inline-block;
      line-height: 1.1;

      &:not(:last-child) {
        a {
          @include margin(null 1em null null);
        }
      }
    }

    a {
      &::after {
        content: "\f019";
      }
    }
  }

  // Download and request buttons share same colors, just with different icons
  .download,
  .request {
    a {
      background-color: $su-color-lagunita;

      &:hover,
      &:focus {
        background-color: $su-color-chocolate;
      }
    }
  }

  .request {
    a {
      &::after {
        content: "\f138";
        font-size: 0.9em;
      }
    }
  }

  .wp-block-column {
    .download {
      @include grid-media("sm") {
        text-align: right;
      }

      @include grid-media-between("md", "xl") {
        li {
          display: block;

          a {
            @include margin(null 0 null null);
          }
        }
      }
    }
  }

  // Logo downloads related
  .dept-branding img {
    border: 1px solid $su-color-cool-grey-25;
  }

  .workmark-usage-2col,
  .seal-usage-2col {
    figure {
      @include margin(null null 0);
    }

    .wp-block-group {
      border: 1px solid $su-color-cool-grey-25;
    }
  }

  .workmark-usage-2col {
    .wp-block-group {
      &.has-background {
        @include padding(1.2em 1.4em);
      }
    }
  }

  .dept-branding {
    img {
      @include padding(null null 1.5rem 1rem); // Adding padding for department branding thumbnails created by John
    }
  }

  // Common styles for jump, download and order button classes
  .jump,
  .download,
  .order,
  .request {
    a {
      @include padding(1rem 1.6rem);
      font-size: 0.9em;
      font-weight: $su-font-semi-bold;
      box-shadow: #ccc 0 2px 2px;
    }
  }

  .jump,
  .download,
  .request {
    a {
      @include padding(1rem 1.6rem);
      display: inline-block;
      color: $su-color-white;
      text-decoration: none;
      text-align: center;

      &:hover,
      &:focus {
        text-decoration: underline;
      }

      &::after {
        @include margin(null null null 0.4em);
        font-weight: 900;
      }
    }
  }

  .wp-block-buttons {

    // Add margin-bottom to when Gutenberg buttons group has the "jump" class added
    &.jump {
      @include margin(null null 1.4em);
    }
  }

  // Styles for clickable image links, e.g., on "Graphic Elements" page, unless the figure has the .no-icon class
  .wp-block-image {
    &:not(.no-icon) {
      > a {
        display: block;
        position: relative;
        border: 1px solid #e6e6e6;
        box-shadow: #dfdfdf 0 0 10px;

        // Adding the magnifying glass overlay in the corner
        &::after {
          position: absolute;
          bottom: 2%;
          right: 2%;
          font-family: "Font Awesome 5 Free";
          content: "\f00e";
          font-size: 2em;
          font-weight: 900;
          opacity: 0.5;
          color: get-color(text--high-contrast);
          text-shadow: $su-color-white 0 0 12px;
        }

        &:hover,
        &:focus {
          border: 1px solid $su-color-driftwood;

          &::after {
            color: get-color(brand--bright);
            opacity: 0.8;
          }
        }
      }
    }
  }

  // Class for adding a light grey border around an image, e.g., sometimes for when the image has a white background
  // Also if adding image links to images on the sidebar widget (e.g., the Logo art sheet widget), add the same border around it
  .img-border,
  .img-link img {
    border: 1px solid $su-color-cool-grey-25;
  }

  .img-link {
    img {
      @include margin(null null 1.4em);
    }
  }

  img {
    &.alignnone {
      @include margin(null null null 0);
    }
  }

  // For the rare cases when you want to center align the caption, e.g., on Mobile Apps page for the icons when the caption is really short
  .center-caption {
    figcaption {
      text-align: center;
    }
  }

  // Photography page Gutenberg columns block/gallery/photo styles
  .photography {
    @include margin(2em null null);

    figure {
      @include margin(null null 0);
    }

    .wp-block-gallery {
      figcaption {
        text-align: left;
      }
    }
  }

  .post__content {
    .photography {
      .wp-block-gallery,
      .wp-block-gallery ul > li:last-child {
        @include margin(null null 0);
      }
    }
  }

  // Vertical arrow spectrum on "Photography" page
  .spectrum {
    font-weight: $su-font-bold;
    font-size: 1.25em;
    color: $su-color-bright-red;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .vertical-arrow {
      @include margin(0 auto);
      display: block;
      width: 2px;
      height: 100%;
      background-color: $su-color-bright-red;
      color: transparent;
    }

    .controlled {
      @include margin(0 null null);

      // Creates the triangular tip of the vertical arrow
      &::before {
        @include margin(0 auto 0.6em);
        content: '';
        display: block;
        width: 0;
        height: 0;
        border-left: 7px solid transparent;
        border-right: 7px solid transparent;
        border-top: 20px solid $su-color-bright-red;
      }
    }
  }

  //
  // ------------------------------------
  // Brand Statement page styles
  // ------------------------------------
  //
  .light-heading {
    @include modular-spacing("margin", 3 null null);

    @include grid-media-only("xs") {
      @include margin(null null 0);

      h2 {
        text-align: center;
      }

      .wp-block-column:last-child {
        display: none;
      }
    }

    @include grid-media("sm") {
      h2 {
        @include modular-typography(1);
      }
    }
  }

  // Style of the brand statements in red
  .essence {
    @include margin(null null 0.6em);
    font-weight: $su-font-semi-bold;
    line-height: 1.3;

    @include grid-media-only("xs") {
      text-align: left;
    }
  }

  // Style of the "context" - text in light grey next to the red brand statements
  .context {
    @include padding(null null null 0.8em);
    font-size: 0.9em;
    border-left: 1px solid $su-color-cool-grey-25;
  }

  // Voice and Tone page styles under "Personality"
  .brand-essence {
    .wp-block-column {
      @include margin(null null 1em);

      h3 {
        @include margin(0 null null);

        @include grid-media-only("xs") {
          text-align: left;
        }
      }
    }
  }

  // Back to top link style
  #wpfront-scroll-top-container {
    .text-holder {
      text-align: center;
      font-size: 0.8em;
      font-weight: 600;
      background-color: transparent!important;
      box-shadow: none!important;

      &::before {
        content: "\f35b";
        display: block;
        font-family: "Font awesome 5 free";
        color: #000;
        background: transparent;
        font-size: 2.6em;
        font-weight: 900;
      }
    }
  }

  //
  // ------------------------------------
  // Homepage styles
  // ------------------------------------
  //
  &.home {
    // No padding bottom for homepage because we have that curved graphic element sitting right on top of the local footer
    main {
      @include padding(null null 259px);
      background: url("/wp-content/uploads/2020/07/graphic-bg.png") #fff 40% bottom no-repeat;
    }

    // The "I would like to" section
    #how-to {
      @include margin(null null 0);

      h2 {
        @include margin(null null 1.25em);
      }

      // Add some spacing after the first row of infoboxes
      .wp-block-columns {
        &:first-of-type {
          @include margin(null null 2em);
        }
      }

      // Color of the infobox icons
      .widget_info_box_widget {
        > span {
          &:first-child {
            color: $su-color-bright-red;
          }
        }
      }
    }

    @include grid-media-only("sm") {
      h3 {
        @include modular-typography(1);
      }
    }

    @include grid-media-between("sm", "md") {
      main {
        .widget_info_box_widget {
          flex-direction: column;

          > span {
            &:first-child {
              @include margin(null null 0.3em);
            }
          }
        }
      }
    }


    // The "Other helpful resources" section
    #other-resources {
      @include margin(0 null);
      @include padding(null null 1em);
    }
  }

  // Homepage banner custom style
  .bannertext {
    @include padding(3em null 1.8em);
    @include modular-typography(0);
    background: url('/wp-content/themes/redwood/assets/img/banners/twilight.jpg') #000 center center no-repeat; // the night palm tree image
    background-size: cover;
    text-align: center;

    // The color or gradient overlay on the banner image (usually to darken it a bit so the icon links are more legible)
    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0, 0.3) 40%, rgba(0,0,0,0) 100%);
      opacity: 0.9;
    }

    h1,
    .quick-link {
      position: relative;
    }

    h1 {
      @include margin(null null 2.2em);
      @include modular-typography(4);
      font-weight: $su-font-bold;
      text-shadow: rgba(15, 8, 68, 0.6) 0 0 10px;

      @include grid-media-only("xs") {
        @include margin(null null 1em);

      }

      @include grid-media-max("md") {
        @include modular-typography(3);
      }
    }

    // Each icon link group in the banner area
    .quick-link {
      @include margin(null null 1em);

      a {
        color: $su-color-white;
        text-decoration: none;
      }

      p {
        line-height: 1.1;

        a {
          @include modular-typography(1);
          font-weight: $su-font-bold;

          &:focus {
            text-decoration: underline;
          }

        }
      }

      @include grid-media-only("sm") {
        p {
          a {
            font-size: 0.97em;
          }
        }
      }

      @include grid-media-only("md") {
        p {
          a {
            font-size: 1.1em;
          }
        }
      }

      &:hover {
        p {
          a {
            text-decoration: underline;
          }
        }
      }

      i {
        @include margin(0.24em);
      }
    }
  }

  // Homepage section right below the hero area with the ID guide download
  #homepage-intro {
    @include margin(null null 0);

    // The brand guide cover that is rotated
    img {
      transform: rotate(-3deg) scale(0.92);
      box-shadow: #bbb 2px 2px 4px;
    }

    figure {
      a {
        &:hover,
        &:focus {
          img {
            transform: scale(0.92);
            opacity: 1;
          }
        }
      }
    }

    // Make that download button a bit larger than the ones on interior pages
    .download {
      a {
        @include padding(0.7em 1.2em);
        font-size: 1.1em;
      }
    }
  }

  //
  // ------------------------------------
  // Color swatches related styles
  // ------------------------------------
  //

  // Logo color match swatches (mini 5 color swatches on logo pages)
  .logo-colors {
    p {
      @include margin(0.2em null);
      text-align: center;
      font-size: 0.74em;
      font-weight: $su-font-semi-bold;

      &.has-background {
        @include padding(2.3em 0.2em 0.4em);
        @include margin(null null 0);
      }
    }
  }

  // Add a light border for swatches or logo container that has a white background
  .white {
    box-shadow: #000 inset 0 0 1px;
  }

  // Color swatches on Accent Colors, Primary Colors pages etc.
  .colors {
    @include margin(1.4em null 3em);

    .wp-block-group__inner-container {
      @include padding(1em);

      // Color values inside each swatch are listed using <ul>
      ul {
        @include padding(null null null 0);
        @include margin(null null 0);
        list-style-type: none;
      }

      li {
        // Set same width for "PMS", "RGB", "CMYK" and "HEX" (these are bolded) so the values after them line up
        > strong {
          display: inline-block;
          width: 3em;
          font-weight: $su-font-semi-bold;

          // Between the MD and LG breakpoint, break color values to the 2nd line
          @include grid-media-between("md", "lg") {
            display: block;
          }
        }
      }
    }

    h2 {
      font-size: 1.4em; // Main color name inside a swatch, e.g., Cardinal Red
    }

    //The main color swatch (not the light or dark version) has a larger top padding for SM and up breakpoints
    @include grid-media("sm") {
      .wp-block-group:first-of-type {
        .wp-block-group__inner-container {
          @include padding(5em null null);
        }
      }
    }
  }

  // Styles for gradient swatches on Primary Colors page
  .gradient {
    @include padding(1em);

    p {
      @include margin(null null 0);

      > strong {
        display: inline-block;
        width: 2.4em;
        font-weight: $su-font-semi-bold;
      }
    }
  }

  .colors,
  .gradient {

    // font sizes for the word "Dark", "Light" inside the dark and light version of the colors, and the title of the gradient swatches
    h3 {
      @include modular-typography(1);
    }
  }

  .colors ul,
  .gradient p {
    font-size: 0.9em;
  }

  // Cardinal Red swatch is a 1-column grid
  .wp-block-columns {
    &.has-1-column {
      grid-template-areas: "col-1";
      grid-template-columns: 1fr;
      grid-column-gap: 0;
    }
  }

  @include grid-media-only("md") {
    .colors h2,
    .colors h3,
    .gradient h3 {
      font-size: 1.1em;
    }
  }

}
